.form {
  background: #e2e2e2;
  padding: 20px;
  width: 100vw;
}

.form-input {
  border: none;
  margin: 10px;
  padding: 5px;
}

.form-submit {
  border: none;
  background-color: blueviolet;
  color: beige;
  padding: 5px 10px;

}
.Lista {
    padding: 20px;
    list-style-type: none;
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    flex-wrap: wrap;
}

.lista-taller {
    border: 1px #e2e2e2 solid;
    box-shadow: 5px 5px 3px #5d5d5d;
    width: 50vw;
}

.lista-taller h3 {
    color: #3390fa;
    /* background-color: black; */
}
.lista-taller div {
    background-color: aliceblue;
    text-align: left;
    padding: 10px 5px;
}

@media screen and (max-width: 720px){
    .Lista-taller {
        width: 100%;
    }
}
